<script setup>
import { onMounted, reactive, ref, watch } from 'vue';
import axios from 'axios';
let tokenjwt = localStorage.getItem('token');

let Contracts=ref();
onMounted(() => {
    axios.get(`${process.env.VUE_APP_HOST_BACKEND}/statics/get/Dashboard`,  {
        headers: {
        Authorization: tokenjwt,
        },
    })
    .then((response) => {
        Contracts.value = response.data.data;
        
    })
    .catch((error) => {
        console.log(error);
    });
});
</script>

<template>
    this is dashbord Payment 
</template>
